import debounce from 'lodash.debounce';
import { useCallback, useEffect, useRef } from 'react';
import { useParams, useSearchParams } from 'react-router-dom';

import './searchInput.scss';

const SearchInput = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const inputRef = useRef(null);

  const { categoryId, sortParam } = useParams();
  const onInputChange = () => {
    setSearchParams({search:inputRef.current.value});
  }
  const onClear = () => {
    inputRef.current.value = "";
  };
  const debounceInputChange = useCallback(
    debounce(onInputChange, 400)
    , []);
  // useEffect(() => {
  //   debounceInputChange();
  // }, [categoryId, sortParam])


  return (
    <div className="categories__search categories-search">
      <svg width="49" height="49" viewBox="0 0 49 49" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M49 44.435L32.7984 28.2239C34.8692 25.2125 35.9606 21.6349 35.9238 17.9797C35.9254 13.963 34.5822 10.0615 32.1086 6.89794C29.635 3.7344 26.1735 1.49116 22.2767 0.526267C18.3799 -0.438624 14.2723 -0.0695463 10.6097 1.57459C6.947 3.21873 3.94036 6.04315 2.06962 9.59705C0.198879 13.1509 -0.428118 17.2295 0.288703 21.1816C1.00552 25.1338 3.02484 28.7317 6.02436 31.4013C9.02387 34.0708 12.8307 35.658 16.8372 35.9095C20.8436 36.161 24.8188 35.0624 28.1283 32.7889L44.3299 49L49 44.435ZM5.10119 17.9797C5.10834 15.4189 5.87471 12.9177 7.30327 10.793C8.73183 8.66817 10.7583 7.01528 13.1263 6.04354C15.4942 5.0718 18.097 4.8249 20.6052 5.3341C23.1135 5.84331 25.4143 7.08571 27.2165 8.90404C29.0186 10.7224 30.241 13.0348 30.729 15.5487C31.217 18.0625 30.9485 20.6647 29.9576 23.0259C28.9667 25.387 27.2979 27.4008 25.1625 28.8124C23.0272 30.224 20.5212 30.9699 17.9619 30.9557C14.5382 30.9367 11.2618 29.5601 8.85092 27.1276C6.44009 24.6951 5.09163 21.4054 5.10119 17.9797V17.9797Z" fill="white" />
      </svg>
      <input ref={inputRef} name='search' onChange={debounceInputChange} className="categories-search__input" placeholder="Search Clothes" type="text" />
      <button onClick={onClear} className="clear-input">
        <svg className='search-input__clear' width="38" height="38" viewBox="0 0 38 38" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M1.64645 4.35681L1.29289 4.00326L1.64645 3.6497L3.6497 1.64645L4.00326 1.29289L4.35681 1.64645L19 16.2896L33.6432 1.64645L33.9967 1.29289L34.3503 1.64645L36.3536 3.6497L36.7071 4.00326L36.3536 4.35681L21.7104 19L36.3536 33.6432L36.7071 33.9967L36.3536 34.3503L34.3503 36.3536L33.9967 36.7071L33.6432 36.3536L19 21.7104L4.35681 36.3536L4.00326 36.7071L3.6497 36.3536L1.64645 34.3503L1.29289 33.9967L1.64645 33.6432L16.2896 19L1.64645 4.35681Z" fill="white" stroke="white" />
        </svg>
      </button>
    </div>
  )
}
export default SearchInput;