import React from 'react'
import Header from '../../components/Header/Header';
import HomeSlider from '../../components/HomeSlider/HomeSlider';
import HomeCategories from '../../components/HomeCategories/HomeCategories';

import './homePage.scss';
 const HomePage = () => {
  return (
    <>
      <Header neededContent={true}/>
      <section className="trends">
        <div className="container">
          <div className="trends__inner">
            <h2 className="trends__title">TRENDS <span>2023</span></h2>

            <HomeSlider feature='trends' />

          </div>
        </div>
      </section>
      <section className="home-categories">
        <div className="container">
          <div className="home-categories__inner">
            <h2 className="home-categories__title">CATEGORIES</h2>
            
            <HomeCategories/>
            
          </div>
        </div>
      </section>
      <section className="monochrome">
        <div className="container">
          <div className="monochrome__inner">
            <h2 className="monochrome__title">MONO <span>CHROME</span></h2>
            <h3 className="monochrome__subtitle">COLLECTION</h3>

            <HomeSlider feature='monochrome' />

          </div>
        </div>
      </section>
      <section className="hotsummer">
        <div className="container">
          <div className="hotsummer__inner">
            <h2 className="hotsummer__title">HOT<span>SUMMER</span></h2>
            <h3 className="hotsummer__subtitle">COLLECTION</h3>

            <HomeSlider feature='hotsummer' />

          </div>
        </div>
      </section>
    </>
  )
}
export default HomePage;