import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import Header from '../../components/Header/Header';
import { CartSkeleton } from '../../components/Skeletons/CartSkeleton/CartSkeleton';
import './cartPage.scss';

const CartPage = () => {
  const [cartItems, setCartItems] = useState();
  const [clearedCart, setClearedCart] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    onStorage();
    window.addEventListener('storage', onStorage);

  }, []);
  useEffect(() => {
    if (clearedCart) {
      setCartItems(null);
    } else {
      setCartItems(JSON.parse(localStorage.getItem('cartItems')));
    }
  }, [clearedCart]);
  const onStorage = () => {
    if (localStorage.getItem('cartItems')) {
      setCartItems(JSON.parse(localStorage.getItem('cartItems')));
      setClearedCart(false);
    }

  };

  const onCartClear = () => {
    localStorage.clear();
    setClearedCart(true);
  }
  const onDeleteItem = (target) => {
    const curentCartItems = cartItems.filter(element => element.id !== +target.closest('.cart-item').getAttribute('data-item'));
    if (curentCartItems.length === 0) {
      console.log('ahjsj');
      localStorage.removeItem('cartItems');
      setClearedCart(true);
    } else {
      localStorage.setItem('cartItems', JSON.stringify(curentCartItems));
    }
    dispatchEvent(new Event("storage"));
  }
  const onAddMoreItems = (target) => {
    if (cartItems) {
      const currentCartItems = cartItems.map(element => {
        if (element.id === +target.closest('.cart-item').getAttribute('data-item')) {
          element.count += 1
          return element
        } else { return element }
      });
      localStorage.setItem('cartItems', JSON.stringify(currentCartItems));
      window.dispatchEvent(new Event("storage"));
    }
  }
  const onMinusItem = (target) => {
    if (cartItems) {
      const currentCartItems = cartItems.map(element => {
        if (element.id === +target.closest('.cart-item').getAttribute('data-item')) {
          if (element.count === 1) {
            onDeleteItem(target);
          } else {
            element.count -= 1;
            return element;
          }


        }
        else { return element }
      });
      const trueItems = currentCartItems.filter(item => item);
      if (trueItems.length > 0) {
        localStorage.setItem('cartItems', JSON.stringify(currentCartItems));
        window.dispatchEvent(new Event("storage"));
      }


    }
  }


  const cartRender = () => {
    const itemsLayout = cartItems.map(item => {
      return (
        <div key={item.id} data-item={item.id} className="cart-content__item cart-item" >
          <div className="cart-item__info">
            <img className="cart-item_img" src={item.imageUrl} alt={item.title} />
            <div className="cart-item__text">
              <div className="cart-item__naming">{item.title}</div>
              <div className="cart-item__size">M Size</div>
            </div></div>
          <div className="cart-item__buttons">
            <div className="cart-item__count cart-count"><button onClick={(e) => onMinusItem(e.target)} className="cart-count__minus"><svg width="48" height="48" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
              <circle cx="24" cy="24" r="23" fill="white" stroke="black" strokeWidth="2" />
              <path d="M22.5603 22.5605H29.7603C30.5555 22.5605 31.2003 23.2053 31.2003 24.0005C31.2003 24.7958 30.5555 25.4405 29.7603 25.4405H22.5603H18.2403C17.4451 25.4405 16.8003 24.7958 16.8003 24.0005C16.8003 23.2053 17.4451 22.5605 18.2403 22.5605H22.5603Z" fill="black" />
            </svg>
            </button>
              <div className="cart-count__quantity">{item.count}</div>
              <button className="cart-count__plus" onClick={(e) => onAddMoreItems(e.target)}><svg width="48" height="48" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
                <circle cx="24" cy="24" r="23" fill="white" stroke="black" strokeWidth="2" />
                <path d="M29.7603 22.5608H25.4403V18.2408C25.4403 17.4455 24.7955 16.8008 24.0003 16.8008C23.2051 16.8008 22.5603 17.4455 22.5603 18.2408V22.5608H18.2403C17.4451 22.5608 16.8003 23.2055 16.8003 24.0008C16.8003 24.796 17.4451 25.4408 18.2403 25.4408H22.5603V29.7608C22.5603 30.556 23.2051 31.2008 24.0003 31.2008C24.7955 31.2008 25.4403 30.556 25.4403 29.7608V25.4408H29.7603C30.5555 25.4408 31.2003 24.796 31.2003 24.0008C31.2003 23.2055 30.5555 22.5608 29.7603 22.5608Z" fill="black" />
              </svg>
              </button></div>
            <div className="cart-item__price">{item.price}$</div>
            <button onClick={(e) => onDeleteItem(e.target)} className="cart-item__delete"><svg width="48" height="48" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
              <circle cx="24" cy="24" r="23" fill="white" stroke="black" strokeWidth="2" />
              <path d="M29.6215 26.9343L26.2486 23.5614L29.6215 20.1885C30.2424 19.5676 30.2424 18.5608 29.6215 17.9399C29.0006 17.319 27.9938 17.319 27.3729 17.9399L24 21.3128L20.6271 17.9399C20.0062 17.319 18.9994 17.319 18.3785 17.9399C17.7576 18.5608 17.7576 19.5676 18.3785 20.1885L21.7514 23.5614L18.3785 26.9343C17.7576 27.5552 17.7576 28.562 18.3785 29.1829C18.9994 29.8038 20.0062 29.8038 20.6271 29.1829L24 25.81L27.3729 29.1829C27.9938 29.8038 29.0006 29.8038 29.6215 29.1829C30.2424 28.562 30.2424 27.5552 29.6215 26.9343Z" fill="#080808" />
            </svg>
            </button>
          </div>
        </div>)
    })
    return (<div className="cart__content cart-content">
      {itemsLayout}

    </div>)
  }

  const cartItemsLayout = cartItems ? cartRender() : <CartSkeleton />;
  return (
    <>
      <Header clearedCart={clearedCart} />
      <div className="cart">
        <div className="container">
          <div className="cart__inner">
            <div className="cart__top cart-top">
              <h2 className="cart-top__title">CART</h2>
              <div className="cart-top__clear cart-clear">
                <button className="cart-clear__btn" onClick={onCartClear}><svg width="43" height="50" viewBox="0 0 43 50" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M2.37549 11.0547H6.63829H40.7407" stroke="black" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round" />
                  <path d="M13.0319 11.0556V6.52778C13.0319 5.32694 13.481 4.17528 14.2804 3.32616C15.0799 2.47703 16.1641 2 17.2947 2H25.8203C26.9509 2 28.0351 2.47703 28.8346 3.32616C29.634 4.17528 30.0831 5.32694 30.0831 6.52778V11.0556M36.4773 11.0556V42.75C36.4773 43.9508 36.0282 45.1025 35.2288 45.9516C34.4293 46.8007 33.3451 47.2778 32.2145 47.2778H10.9005C9.76993 47.2778 8.68567 46.8007 7.88624 45.9516C7.08681 45.1025 6.6377 43.9508 6.6377 42.75V11.0556H36.4773Z" stroke="black" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round" />
                  <path d="M17.2959 22.375V35.9583" stroke="black" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round" />
                  <path d="M25.8203 22.375V35.9583" stroke="black" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round" />
                </svg>
                </button>
                <div className="cart-clear__text">CLEAR CART</div>
              </div>
            </div>


            {cartItemsLayout}


            <div className="cart__bottom cart-bottom">
              <button className="cart-bottom__back go-back-btn" onClick={() => navigate(-1)}><svg width="14" height="24" viewBox="0 0 14 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M12 22L2 11.8836L11.7696 2" stroke="black" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round" />
              </svg>
                Go Back</button>
              <div className="cart-bottom__total ">Total Price:
                <span id="cart-total">{cartItems ? cartItems.reduce((num, item) => num + (item.price * item.count), 0) : 0}$</span></div>
              <button className="cart-bottom__payment go-to-payment-btn">Go To Payment<svg width="16" height="26" viewBox="0 0 16 26" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M3 3L13 13.1164L3.23042 23" stroke="white" strokeWidth="6" strokeLinecap="round" strokeLinejoin="round" />
              </svg>
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default CartPage;