import { useEffect, useState } from 'react';
import { Link, useParams, useSearchParams } from 'react-router-dom';

import GetService from '../../services/GetService';
import setHomeContent from '../../utils/setHomeContent';

import { TabsSkeleton } from '../../components/Skeletons/TabsSkeleton/TabsSkeleton';

import './categoriesTabs.scss';

const CategoriesTabs = () => {
  const [categories, setCategories] = useState();
  const [searchParams, setSearchParams] = useSearchParams();
  const { categoryId, sortParam } = useParams();
  const { process, setProcess, getHomeCategories } = GetService();
  useEffect(() => {
    getCategories();
  }, [])
  const getCategories = () => {
    getHomeCategories()
      .then(setCategories)
      .then(() => setProcess('loaded'))
  }
  const renderCategories = (categories) => {
    return categories.map((item, i) => {
      return (
        <Link to={`/categories/${i + 1}${sortParam ? `/${sortParam}` : ''}${searchParams.get('search') ? `?search=${searchParams.get('search')}` : ''}`} key={item.id} className={+categoryId === i + 1 ? "categories-tabs__item active-tab" : "categories-tabs__item"}>{item.title}
        </Link>)
    })
  }
  return (
    <>
      <Link to={`/categories/0${sortParam ? `/${sortParam}` : ''}${searchParams.get('search') ? `?search=${searchParams.get('search')}` : ''}`} className={+categoryId === 0 ? "categories-tabs__item active-tab" : "categories-tabs__item"}>ALL</Link>
      {setHomeContent(process, () => renderCategories(categories), TabsSkeleton)}</>
  )
}
export default CategoriesTabs;