import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import Header from '../../components/Header/Header';
import CategoriesTabs from '../../components/CategoriesTabs/CategoriesTabs';
import CategoriesPageItems from '../../components/CategoriesPageItems/CategoriesPageItems';
import SortItems from '../../components/SortItems/SortItems';
import CategoriesTitle from '../../components/CategoriesTitle/CategoriesTitle';
import SearchInput from '../../components/SearchInput/SearchInput';

import GetService from '../../services/GetService';


import './categoriesPage.scss';

const CategoriesPage = () => {
  return (
    <>
      <Header />
      <div className="categories">
        <div className="container">
          <div className="categories__inner">
            <nav className="categories__tabs categories-tabs">
              <CategoriesTabs />
            </nav>
            <div className="categories__filtering">
              <SearchInput />
              <SortItems />
            </div>
            <CategoriesTitle />
            <CategoriesPageItems />


          </div>
        </div>
      </div>
    </>
  )
}
export default CategoriesPage;