const setHomeContent = (process, Component, Skeleton, ref) => {
  switch (process) {
    case 'waiting':
      return <Skeleton ref={ref}/>;
    case 'loading':
      return <Skeleton ref={ref}/>;
    case 'loaded':
      return <Component/>;
    case 'error':
      return <h6>Something went wrong. Please try again later!</h6>;
    default:
       throw new Error('Unexpected process state');;
  }
}
export default setHomeContent;