import  useHttps  from '../hooks/useHttps'
 const GetService = () => {
  const _apiBase = 'https://62aaffb1a62365888bd1d87a.mockapi.io/';
  const {process, setProcess, request, clearError} = useHttps();
  const getHomeSliderItems = async(feature) => {
    const res = await request(`${_apiBase}collections`);
    return res.filter(item => feature in item)
  }
  const getHomeCategories =  async() => {
    return await request(`${_apiBase}categories`);
}
const getAllClothes = async() => {
  return await request(`${_apiBase}all`)
}


  return ({process, setProcess, getHomeSliderItems, getHomeCategories,getAllClothes, clearError});
}
export default GetService;