import {useState, useEffect, useRef} from 'react';
import { useParams, Link , useSearchParams} from 'react-router-dom';
import './sortItems.scss';
 const SortItems = () => {
  const [activeSort, setActiveSort] = useState();
  const [searchParams, setSearchParams] = useSearchParams();

  const {sortParam, categoryId} = useParams();
  
  const sortBody = useRef();

  useEffect(() => {
    /**
     * Alert if clicked on outside of element
     */
    function handleClickOutside(event) {
      if (sortBody.current && !sortBody.current.contains(event.target)) {
        setActiveSort(false);
      }
    }
    // Bind the event listener
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [sortBody]);
  
  const toggleSort = () => {
    setActiveSort(prevState => !prevState)
  }
  return (
    <div onClick={toggleSort} className="sort-select">
      <div className="sort-select__header">
        <div className="sort-select__current">Sort By: {sortParam ? sortParam.replace('&', ' '): null}<span className="sort-select__chosen"></span></div>
        <svg width="45" height="26" viewBox="0 0 45 26" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" clipRule="evenodd" d="M25.4534 24.349C23.8057 26.0013 21.137 26.0013 19.4865 24.349L1.21984 5.9911C-0.430707 4.33593 -1.15956 0 4.05584 0H40.8897C46.281 0 45.3763 4.33308 43.7257 5.9911L25.4534 24.349Z" fill="#F3F3F3" />
        </svg>

      </div>
      <div ref={sortBody} className={activeSort ?'sort-select__body active-select' : 'sort-select__body'}>
        <Link to={`/categories/${categoryId}/rating&INCR${searchParams.get('search') ? `?search=${searchParams.get('search')}` : ''}`} className="sort-select__option">- rating INCR</Link>
        <Link to={`/categories/${categoryId}/rating&DECR${searchParams.get('search') ? `?search=${searchParams.get('search')}` : ''}`} className="sort-select__option">- rating DECR</Link>
        <Link to={`/categories/${categoryId}/price&INCR${searchParams.get('search') ? `?search=${searchParams.get('search')}` : ''}`} className="sort-select__option">- price INCR</Link>
        <Link to={`/categories/${categoryId}/price&DECR${searchParams.get('search') ? `?search=${searchParams.get('search')}` : ''}`} className="sort-select__option">- price DECR</Link>
      </div>
    </div>
  )
}
export default SortItems;