import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import GetService from '../../services/GetService';
import setHomeContent from '../../utils/setHomeContent';

import {HomeCategoriesSkeleton} from  '../Skeletons/HomeCategoriesSkeleton/HomeCategoriesSkeleton';

const HomeCategories = () => {
  const [categories, setCategories] = useState();
  const { process, setProcess, getHomeCategories } = GetService();

  useEffect(() => {
    getCategories();


  }, [])

  const getCategories = () => {
    getHomeCategories()
      .then(setCategories)
      .then(() => setProcess('loaded'));
  }
  const renderCategories = (categories) => {
    return categories.map((item, i)=> {
      return (
        <Link className="home-categories__card-link" key={item.id} to={`/categories/${i+1}`}>
        <div className="home-categories__card">
            <img className="home-categories__img" src={item.imageUrl} alt={item.title}/>
            <div className="home-categories__name">{item.title}</div>
        </div>
      </Link>
      )
    })
  }
  return (
    <>
    <div className="home-categories__cards">
{setHomeContent(process, () => renderCategories(categories), HomeCategoriesSkeleton)}</div></>
  )
}
export default HomeCategories;
