import { useState, useEffect, useRef } from "react";
import { Link, useParams, useSearchParams } from "react-router-dom";


import GetService from "../../services/GetService";
import setHomeContent from '../../utils/setHomeContent';
import NoItemsFound from '../NoItemsFound/NoItemsFound';

import { CategoriesItemSkeleton } from "../Skeletons/CategoriesItemSkeleton/CategoriesItemSkeleton";

import './categoriesPageItem.scss';

const CategoriesPageItems = () => {
  const [items, setItems] = useState();

  const [loadMore, setLoadMore] = useState(8);
  let [searchParams, setSearchParams] = useSearchParams();

  const { categoryId, sortParam } = useParams();

  const loadMoreBtn = useRef();

  const { process, setProcess, getAllClothes } = GetService();

  useEffect(() => {
    getItems();
  }, [])
  useEffect(() => {
    // getItems();
    setLoadMore(8);
  }, [sortParam, categoryId])
  const getItems = () => {
    getAllClothes()
      .then(setItems)
      .then(() => setProcess('loaded'))
  }
  const filterItemsBySearch = (items) => {
    return items.filter(item => item.title.toLowerCase().includes(searchParams.get('search').toLowerCase()))

  }
  const prepareItems = (items) => {
    const trueItems = items.filter(item => item);
    let preparedItems;
    if (Math.floor(trueItems.length / loadMore) >= 1) {
      preparedItems = trueItems.slice(0, loadMore);
      loadMoreBtn.current.style.display = 'inline-flex';
    } else {
      preparedItems = trueItems.slice(0, trueItems.length);
      loadMoreBtn.current.style.display = 'none';
      // setLoadMore(8);
    }
    return preparedItems.map(item => {
      return (
        <Link to={`/items/${item.id}`} key={item.id} className="categories-content__item-link">
          <div className="categories-content__item categories-item" >
            <img className="categories-item__img" src={item.imageUrl[0]} alt={item.title} />
            <div className="categories-item__naming">{item.title}</div>
            <div className="categories-item__price">{item.price} $</div>
          </div>
        </Link>
      )
    })

    // for (let i = 0; i < preparedItems.length; i++) {
    //   return (
    //     <Link to={`/items/${preparedItems[i].id}`} key={preparedItems[i].id} className="categories-content__item-link">
    //       <div className="categories-content__item categories-item" >
    //         <img className="categories-item__img" src={preparedItems[i].imageUrl[0]} alt={preparedItems[i].title} />
    //         <div className="categories-item__naming">{preparedItems[i].title}</div>
    //         <div className="categories-item__price">{preparedItems[i].price} $</div>
    //       </div>
    //     </Link>
    //   )
    // }
  }



  const sortItems = (items) => {
    const preparedItems = items.filter(item => item);
    if (sortParam === 'rating&INCR') {
      return preparedItems.sort((a, b) => {
        return a.rating - b.rating;
      });
    } if (sortParam === 'rating&DECR') {
      return preparedItems.sort((a, b) => {
        return b.rating - a.rating;
      });
    } if (sortParam === 'price&INCR') {
      return preparedItems.sort((a, b) => {
        return a.price - b.price;
      });
    } if (sortParam === 'price&DECR') {
      return preparedItems.sort((a, b) => {
        return b.price - a.price;
      });
    }
  }
  const renderItems = (items) => {
    const filteredItems = items.map(item => {
      if (+categoryId === 0) {
        return item
      } else {
        if (+item.category === +categoryId) {
          return item
        } else {
          return null
        }
      }
    });
    const sorted = sortParam ? sortItems(filteredItems) : filteredItems;
    const filteredBySearch = searchParams.get('search') ? filterItemsBySearch(sorted) : sorted;
    const result = prepareItems(filteredBySearch);
    return (
      <>
        <div className="categories__content categories-content">
          {result.length > 0 ? result :<NoItemsFound /> }
        </div>


      </>
    )
  }


  return (
    <>
      {setHomeContent(process, () => renderItems(items), CategoriesItemSkeleton)}
      <button ref={loadMoreBtn} onClick={() => setLoadMore(prevState => prevState + 8)} className="categories-load-more">LOAD MORE ... <svg width="55" height="55" viewBox="0 0 55 55" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M22.3586 24.3862H23.3586V24.1229C23.9732 23.7741 24.3872 23.1136 24.3872 22.3575V8.17122C24.3872 6.45461 25.7846 5.05707 27.5011 5.05707C29.2179 5.05707 30.6153 6.4545 30.6153 8.17122V22.3576C30.6153 23.4768 31.5224 24.3862 32.6438 24.3862H46.827C48.5438 24.3862 49.9412 25.7834 49.9412 27.5003C49.9412 29.2169 48.5439 30.6144 46.827 30.6144H32.6438C31.5225 30.6144 30.6153 31.5237 30.6153 32.6428V46.8292C30.6153 48.546 29.218 49.9434 27.5011 49.9434C25.7846 49.9434 24.3872 48.5461 24.3872 46.8292V32.6428C24.3872 31.5236 23.4804 30.6144 22.3586 30.6144H8.17122C6.4545 30.6144 5.05707 29.217 5.05707 27.5003C5.05707 25.7836 6.4545 24.3862 8.17122 24.3862H22.3586ZM1 27.5001C1 31.4553 4.21531 34.6712 8.17122 34.6712H20.3301V46.829C20.3301 50.7843 23.5454 54.0003 27.5011 54.0003C31.4576 54.0003 34.6724 50.7843 34.6724 46.829V34.6712H46.827C50.7824 34.6712 53.9983 31.4553 53.9983 27.5001C53.9983 23.5442 50.7824 20.3289 46.827 20.3289H34.6722V8.17122C34.6722 4.21542 31.4574 1 27.5011 1C23.5454 1 20.3301 4.21536 20.3301 8.17122V20.3291H8.17122C4.21555 20.3291 1 23.5444 1 27.5001Z" fill="white" stroke="white" strokeWidth="2" />
      </svg>
      </button>
    </>
  )
};
export default CategoriesPageItems;