import React from 'react'

import './noItemsFound.scss'

 const NoItemsFound = () => {
  return (
    <div className="no-items">
    <h3 className="no-items__message">No items matching your search were found</h3>
    <p className="no-items__check">Try to check everything again</p>
</div>
  )
}
export default NoItemsFound